@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Oswald);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
}

.intro {
    min-width: 1024px;
    min-height: 100%;
    width: 100%;
    height: auto;
    position: fixed;
    left: 0;
    margin: auto;
    background-image: url(/static/media/Desk.ea1af375.jpg);
    display: table;
    background-size: cover;
}

.intro .inner {
    display: table-cell;
    vertical-align: auto;
    width: 100%;
    max-width: none;
}

.content {
    max-width: 700px;
    margin: 0 auto;
    text-align: center;
}

.content h1 {
    font-family: 'Raleway', sans-serif;
    color: #4050B1;
    text-shadow: 0px 0px 300px #000;
    font-size: 500%;
    margin-bottom: 10px;
}

.content h3 {
  margin-bottom: 150px;
}

.content h4 {
    font-family: 'Raleway', sans-serif;
    color: #f9f3f4;
    text-shadow: 0px 0px 300px #000;
    font-size: 100%;
    margin-bottom: 150px;
    font-weight: bold;
}

.btn {
    border-radius: 9px;
    font-family: 'Oswald', sans-serif;
    color: #036a81;
    font-size: 135%;
    padding: 10px 20px;
    border: solid #036A81 3px;
    text-transform: uppercase;
    text-decoration: none;
    
}

.signin{
    font-family: 'Oswald', sans-serif;
    color: #036a81;
    text-decoration: none;
    font-weight: bold;
}

.btn:hover{
    color: #fff;
    border: solid #fff 3px;
}
.loader {    
    min-height: 100%;
    width: 100%;
    height: auto;
    position: fixed;
    left: 0;
    margin: auto;
    background-image: url(/static/media/Loading.3e581ebe.jpg);
    display: table;
    background-size: cover;
}
